<template>
    <div class="main-page">
        <template v-if="showHeader">
            <q-card   class="page-section q-py-sm q-px-md q-mb-md nice-shadow-18">
                <div class="container">
                    <div class="row justify-between q-col-gutter-md">
                        <div class="col-12 col-md-auto ">
                            <div class="">
                                <div class="row  items-center q-col-gutter-sm q-px-sm">
                                    <div class="col">
                                        <div class="text-h6 text-primary">Add New Access Lists</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </q-card>
        </template>
        <section class="page-section q-mb-md">
            <div class="container">
                <div class="row q-col-gutter-md">
                    <div class="col-md-9 col-12 comp-grid">
                        <q-card   class="q-pa-md nice-shadow-18">
                            <div>
                                <ValidationObserver ref="observer" v-slot="{ invalid }" tag="form" @submit.prevent="startSaveRecord()" @reset="resetForm">
                                    <div>
                                        <q-markup-table dense flat>
                                        <thead>
                                            <tr>
                                                <th><label for="parent_id">Parent Id</label></th>
                                                <td style="width:30px"></td>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(formData, row) in arrFormData" :key="row" class="input-row">
                                                <td id="ctrl-parent_id-holder">
                                                    <ValidationProvider :rules="{required:true}" name="Parent Id" v-slot="{ errors, invalid, validated }">
                                                        <q-input outlined dense  ref="ctrlparent_id" v-model="formData.parent_id"  label="Parent Id" type="number" placeholder="Enter Parent Id"   step="any" list="parent_id_list"  
                                                        class="" :error="invalid && validated" :error-message="errors[0]">
                                                        </q-input>
                                                        <datalist id="parent_id_list">
                                                        <option v-for="(menu, index) in $menus.nameItems" :key="index" :value="menu.value">{{ menu.label }}</option>
                                                        </datalist>
                                                    </ValidationProvider>
                                                </td>
                                                <th>
                                                <q-btn    :rounded="false"  no-caps  unelevated    padding="xs" v-if="arrFormData.length > minRowCount" @click="removeFormRow(row)" icon="close" size="sm" color="red"></q-btn>
                                                </th>
                                            </tr>
                                        </tbody>
                                        <tfoot>
                                        <tr>
                                            <td colspan="100" class="text-right">
                                                <q-btn    :rounded="false"  no-caps  unelevated   color="positive" padding="xs" v-if="arrFormData.length < maxRowCount" @click="addFormRow()" icon="add" size="sm"></q-btn>
                                            </td>
                                        </tr>
                                        </tfoot>
                                        </q-markup-table>
                                    </div>
                                    <div v-if="showSubmitButton" class="text-center q-my-md">
                                        <q-btn    :rounded="false"  color="primary"  no-caps  unelevated   type="submit" :disabled="invalid" icon="send" :loading="saving">
                                            <q-spinner-oval slot="loading" /> Submit
                                        </q-btn>
                                    </div>
                                </ValidationObserver>
                                <slot :submit="submit" :saving="saving"></slot>
                            </div>
                        </q-card>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>
<script>
	import { PageMixin } from "../../mixins/page.js";
	import { AddPageMixin } from "../../mixins/addpage.js";
	import { mapActions } from "vuex";
	export default {
		name: 'addAccesslistsPage',
		components: {
		},
		mixins: [PageMixin, AddPageMixin ],
		props:{
			pageName: {
				type: String,
				default: 'access_lists',
			},
			routeName: {
				type: String,
				default: 'access_listsadd',
			},
			apiPath : {
				type: String,
				default: 'access_lists/add',
			},
			maxRowCount: {
				type : Number,
				default: 10,
			},
			minRowCount: {
				type : Number,
				default: 1,
			},
		},
		data() {
            return {
				arrFormData: [{
					parent_id: "", 
				}],
			}
		},
		computed: {
			pageTitle:{
				get: function () {
					return "Add New Access Lists"
				}
			},
		},
		meta () {
			return {
				title: this.pageTitle
			}
		},
		methods: {
			...mapActions('access_lists', ['saveRecord']),
			async startSaveRecord(){
				const isFormValid = await this.$refs.observer.validate();
				if(isFormValid){
					this.saving = true;
					let payload = this.normalizedFormData();
					let url = this.apiUrl;
					let data = { url, payload  }
					this.saveRecord(data).then((response) => {
						this.saving = false;
						this.resetForm();
						this.closeDialogs();// close page dialog that if opened
						this.flashMsg(this.msgAfterSave);
						this.navigateTo(`/access_lists`);
					},
					 (response) => {
						this.saving = false;
						this.showPageRequestError(response);
					});
				}
			},
			addFormRow() {
                let newFormData = {parent_id: "", };
				this.arrFormData.push(newFormData);
			},
			removeFormRow (index){
				this.arrFormData.splice(index, 1);
			},
			resetForm (){
				this.arrFormData = [{parent_id: "", }];
				requestAnimationFrame(() => {
					this.$refs.observer.reset();
				});
				this.$EventBus.$emit("resetForm");
			},
		},
		mounted() {
		},
	};
</script>
<style scoped>
</style>
